import request from '@/common/utils/request';
import { buildUrlWithObj } from '@/common/utils/url-utils';

export function queryReasonType(data) {
  return request({
    url: buildUrlWithObj('/fintech/v1/transaction/queryReasonType', data),
    method: 'get',
  });
}

export function searchTransactionByOrderId(data) {
  return request({
    url: '/fintech/v1/searchTransactionByOrderId',
    method: 'post',
    data,
  });
}
