<template>
  <div class="business-portal-page">
    <!-- title -->
    <portal-header :projectObj="titleConfig"></portal-header>
    <!-- Receipt No Search -->
    <el-form
      class="margin-left-20 margin-right-20 margin-top-20"
      :model="receiptNoForm"
      :rules="receiptNoFormRules"
      label-position="top"
      ref="receiptNoFormRef"
      label-width="200px"
      size="small"
    >
      <el-row :gutter="20">
        <el-col :span="8" :xs="colSmall" :sm="colSmall" :md="colSmall" :lg="colSmall" :xl="colXL">
          <el-form-item prop="receiptNo" :label="$t('TRC.reviewTransaction.label.receiptNo')">
            <el-input
              id="receiptNo"
              v-model="receiptNoForm.receiptNo"
              :placeholder="$t('common.placeholder.enter')"
              @keyup.enter.native="searchByReceiptNo"
            ></el-input>
          </el-form-item>
        </el-col>

        <el-col
          :span="8"
          :xs="colSmall"
          :sm="colSmall"
          :md="colSmall"
          :lg="colSmall"
          :xl="colXL"
          style="padding-top: 26px"
        >
          <el-button size="small" plain type="primary" :loading="searchReceiptNoLoading" @click="searchByReceiptNo">
            {{ $t('common.button.review') }}
          </el-button>
        </el-col>
      </el-row>
      <el-divider></el-divider>
    </el-form>
    <!-- body -->
    <!-- :searchBtnDisabled="isSearchBtnDisabled" -->

    <portal-form
      ref="searchList"
      :form-data="searchList"
      :form-rules="formRules"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    >
      <template v-slot:debitParty>
        <el-row :class="['grouped-input']" :gutter="4">
          <el-col :class="['grouped-input-type']" :span="8">
            <el-form-item
              :required="debitRequired"
              :label="$t('TRC.reviewTransaction.label.debitParty')"
              prop="debitPartyType"
            >
              <el-select
                v-model="searchList.debitPartyType"
                :disabled="debitDisabled"
                :placeholder="$t('common.placeholder.select')"
                @change="handSelect"
              >
                <el-option v-for="item in partySelectOptions" :label="item.label" :key="item.value" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :class="['grouped-input-value']" :span="16">
            <el-form-item :class="['hidden-label']" :label="debitInputTitleLabel" :prop="initDebitParty">
              <el-input
                :placeholder="debitInputTitleLabel"
                v-model="searchList.debitParty"
                class="input-with-select"
                readonly="readonly"
              >
                <i
                  v-if="searchList.debitPartyType"
                  class="el-icon-more icon-cursor"
                  slot="suffix"
                  @click="openDebitShortCodeDialog"
                >
                </i>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </template>
      <template v-slot:creditParty>
        <el-row :class="['grouped-input']" :gutter="4">
          <el-col :class="['grouped-input-type']" :span="8">
            <el-form-item
              :required="creditRequired"
              :label="$t('TRC.reviewTransaction.label.creditParty')"
              prop="creditPartyType"
            >
              <el-select
                v-model="searchList.creditPartyType"
                :disabled="creditDisabled"
                :placeholder="$t('common.placeholder.select')"
                @change="handSelectChange"
              >
                <el-option
                  v-for="item in creditSelectOptions"
                  :label="item.label"
                  :key="item.value"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :class="['grouped-input-value']" :span="16">
            <el-form-item :class="['hidden-label']" :label="creditInputTitleLabel" :prop="initCreditParty">
              <el-input
                :placeholder="creditInputTitleLabel"
                v-model="searchList.creditParty"
                class="input-with-select"
                readonly="readonly"
              >
                <i
                  v-if="searchList.creditPartyType"
                  class="el-icon-more icon-cursor"
                  slot="suffix"
                  @click="openCreditShortCodeDialog"
                >
                </i>
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </template>
    </portal-form>
    <div class="table-list-container">
      <div class="margin-bottom-16">
        <el-button id="process" :disabled="!checkedList.length" @click="handleProcess()" size="small" type="primary">
          {{ this.$t('TRC.reviewTransaction.button.process') }}
        </el-button>
      </div>
      <table-page
        size="small"
        ref="data-table"
        class="table-page"
        :has-pagination="hasPagination"
        :columns="columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="refreshTableFlag"
        :isCheck="true"
        :checkSelectable="isSelectable"
        :initializingLoading="false"
        @selection-change="selectTableDataChanged"
      >
        <template slot="orderIdOperation" slot-scope="scope">
          <el-button type="text" @click="showDetail(scope.data.row)" style="padding: 0">
            {{ scope.data.row.tradeOrderId }}
          </el-button>
        </template>
        <template slot="operation" slot-scope="scope">
          <el-button
            v-if="isShowReverse(scope.data.row)"
            type="text"
            class="btn"
            @click="reverse(scope.data.row)"
            style="padding: 0"
          >
            {{ $t('TRC.reviewTransaction.button.reverse') }}
          </el-button>
          <el-button
            v-if="isSelectable(scope.data.row)"
            type="text"
            class="btn"
            @click="goToProcessPage(scope.data.row)"
            style="padding: 0"
          >
            {{ $t('common.button.process') }}
          </el-button>
        </template>
        <template slot="transactionStatus" slot-scope="scope">
          <span class="status-circle" :style="statusStyle(scope.data.row.transactionStatus)"></span>
          <span>{{ scope.data.row.transactionStatus }}</span>
        </template>
      </table-page>
    </div>
    <entity-search-dialog
      :dialog-title="debitDialogTitle"
      :dialogVisible.sync="debitShortCodeVisible"
      :fieldName="debitInputField"
      @select="debitReceiverSelected"
      @onCloseDialog="debitCloseCallBack"
    />
    <entity-search-dialog
      :dialog-title="creditDialogTitle"
      :dialogVisible.sync="creditShortCodeVisible"
      :fieldName="creditInputField"
      @select="creditReceiverSelected"
      @onCloseDialog="creditCoseCallBack"
    />
    <portal-dialog
      top="29vh"
      v-if="showProcessDialog"
      :confirmText="$t('common.button.submit')"
      :visible="true"
      :title="$t('IDC.reviewTransaction.processTitle')"
      :width="'448px'"
      @cancel-btn-click="closeProcessDialog"
      @confirm-btn-click="submitProcessDialog"
      @handle-close="closeProcessDialog"
    >
      <template #content>
        <el-form label-position="top" ref="processingForm" :model="processingForm" :rules="processingFormRules">
          <el-form-item :label="$t('IDC.reviewTransaction.label.action')" prop="action">
            <el-radio-group v-model="processingForm.action" size="small">
              <el-radio v-for="(item, index) in labelActions" :key="index" :label="item.toLowerCase()">
                {{ item }}
              </el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item :label="$t('IDC.bankAccount.label.remark')" prop="remark">
            <el-input
              type="textarea"
              prop="remark"
              :rows="5"
              v-model="processingForm.remark"
              :placeholder="$t('common.placeholder.enter')"
            />
          </el-form-item>
        </el-form>
      </template>
    </portal-dialog>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import entitySearchDialog from '@/views/identifier-center/pages/account-statement/components/entity-search-dialog.vue';

import PortalDialog from '@/components/portal-dialog';

import { inputRequiredValidateObj, selectRequiredValidateObj } from '@/common/validator/common';
import {
  getReceiptNoFormRules,
  getTransactionFormRules,
} from '@/views/identifier-center/validator/reverse-transaction.js';
import {
  queryTransactionList,
  batchApprovalTransaction,
  confirmTransaction,
} from '@/views/identifier-center/api/api-transaction.js';
import { getOrganizationList } from '@/views/identifier-center/api/api-organization-operator';
import { queryReasonType, searchTransactionByOrderId } from '@/views/transaction-center/api/api-transaction.js';
import { verifyTransactionIdentity } from '@/views/identifier-center/api/api-transaction.js';
import AmountUnit from '@/views/business-center/pages/settlement/utils/amount-utils.js';
import dayjs from 'dayjs';

const Identity_type = {
  organization: '5000',
  customer: '1000',
  group: '7000',
};

export default {
  name: 'ReviewTrasaction',
  components: { PortalHeader, TablePage, PortalForm, PortalDialog, entitySearchDialog },
  data() {
    return {
      debitReceiverData: {},
      creditReceiverData: {},
      organizationId: '',
      initCreditParty: 'creditParty',
      initDebitParty: 'debitParty',
      debitDialogTitle: 'Select Organization',
      creditDialogTitle: 'Select Organization',
      debitInputField: 'shortCode',
      creditInputField: 'shortCode',
      searchLoading: false,
      searchReceiptNoLoading: false,
      receiptNoExist: true,
      debitRequired: true,
      creditRequired: true,
      refreshTableFlag: false,
      hasPagination: true,
      debitShortCodeVisible: false,
      creditShortCodeVisible: false,
      showProcessDialog: false,
      debitDisabled: false,
      creditDisabled: false,
      debitShortCode: '',
      receiptNoForm: {
        receiptNo: '',
      },
      receiptNoFormRules: getReceiptNoFormRules(this),
      checkedList: [],
      searchList: {
        initiationTime: this.getInitTime(),
        reasonType: 'all',
        transactionStatus: '',
        debit: null,
        oppositeParty: '',
        debitPartyType: '5000',
        debitParty: '',
        creditPartyType: '5000',
        creditParty: '',
        debitPartyId: '',
        creditPartyId: '',
        oppositePartyId: '',
        oppositePartyType: '',
      },
      entityIdentityType: '',
      tradePartyId: '',
      transactionRole: '',
      processingForm: {
        remark: '',
        action: '',
      },
      processingFormRules: {
        remark: [inputRequiredValidateObj],
        action: [selectRequiredValidateObj],
      },
      debitInputTitleLabel: '',
      creditInputTitleLabel: '',
      columns: [
        {
          label: this.$t('TRC.reviewTransaction.label.receiptNo'),
          prop: 'tradeOrderId',
          align: 'left',
          __slotName: 'orderIdOperation',
          'min-width': 150,
        },
        {
          label: this.$t('TRC.reviewTransaction.label.initiationTime'),
          prop: 'createTime',
          align: 'left',
          'min-width': 133,
        },
        {
          label: this.$t('TRC.reviewTransaction.label.reasonType'),
          prop: 'reasonTypeName',
          align: 'left',
          'min-width': 185,
        },
        {
          label: this.$t('TRC.reviewTransaction.label.debitParty'),
          prop: 'payeeName',
          align: 'left',
          'min-width': 120,
        },
        {
          label: this.$t('TRC.reviewTransaction.label.creditParty'),
          prop: 'payerName',
          align: 'left',
          'min-width': 120,
        },
        {
          label: this.$t('TRC.reviewTransaction.label.status'),
          prop: 'status',
          align: 'left',
          'min-width': 100,
          __slotName: 'transactionStatus',
        },
        {
          label: this.$t('TRC.reviewTransaction.label.amount'),
          prop: 'transactionAmount',
          align: 'right',
          'min-width': 150,
        },
        {
          label: this.$t('TRC.reviewTransaction.label.expirationDate'),
          prop: 'expireTime',
          align: 'left',
          'min-width': 150,
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          'min-width': 93,
        },
      ],
      reasonTypeOptions: [{ label: 'All', value: 'all' }],
      statusOptions: [
        {
          label: 'All',
          value: 'all',
        },
        {
          label: 'Authorized',
          value: 'Authorized',
        },
        {
          label: 'Pending Authorized',
          value: 'Pending Authorized',
        },
        {
          label: 'Refund Pending Authorized',
          value: 'Refund Pending Authorized',
        },
        {
          label: 'Completed',
          value: 'Completed',
        },
        {
          label: 'Cancelled',
          value: 'Cancelled',
        },
        {
          label: 'Declined',
          value: 'Declined',
        },
        {
          label: 'Expired',
          value: 'Expired',
        },
        {
          label: 'Refunded',
          value: 'Refunded',
        },
      ],
      partySelectOptions: [
        {
          label: 'Organization',
          value: Identity_type.organization,
        },
        {
          label: 'Customer',
          value: Identity_type.customer,
        },
        {
          label: 'Group',
          value: Identity_type.group,
        },
      ],
      creditSelectOptions: [
        {
          label: 'Organization',
          value: Identity_type.organization,
        },
        {
          label: 'Customer',
          value: Identity_type.customer,
        },
        {
          label: 'Group',
          value: Identity_type.group,
        },
      ],
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      formRules: getTransactionFormRules(this),
      pickerOptions: {
        disabledDate: (time) => {
          let startTime = dayjs().startOf('date').subtract(14, 'day');
          let endTime = dayjs().endOf('date');
          return time.getTime() < startTime || time.getTime() > endTime;
        },
      },
    };
  },
  computed: {
    isSearchBtnDisabled() {
      const searchType = this.searchList.creditPartyType;
      const permissCodes = {
        5000: 'TC_ORGTRANSACTION_REVIEW_SUBMIT_BTN',
        1000: 'TC_CUS_TRANSACTION_REVIEW_SUBMIT_BTN',
        7000: 'TC_GROUP_TRANSACTION_REVIEW_BTN',
      };
      let result = !this.hasPerm(permissCodes[searchType]);

      return result;
    },
    titleConfig() {
      return {
        title: this.$t('TRC.reviewTransaction.pageTitle'),
        desc: this.$t('TRC.reviewTransaction.pageDesc'),
      };
    },
    formConfigList() {
      return [
        {
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          align: 'left',
          label: this.$t('TRC.reviewTransaction.label.initiationTime'),
          prop: 'initiationTime',
          comp: [
            {
              key: 'initiationTime',
              comp: 'el-date-picker',
              bind: {
                id: 'initiationTime',
                type: 'datetimerange',
                format: 'dd/MM/yyyy HH:mm:ss',
                'range-separator': '~',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': 'Please select',
                'end-placeholder': 'Please select',
                'picker-options': this.pickerOptions,
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
        {
          label: this.$t('TRC.reviewTransaction.label.reasonType'),
          key: 'reasonType',
          comp: 'el-select',
          options: this.reasonTypeOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'reasonType',
          },
        },
        {
          label: this.$t('TRC.reviewTransaction.label.status'),
          key: 'transactionStatus',
          comp: 'el-select',
          options: this.statusOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'status',
          },
        },
        {
          slotName: 'debitParty',
        },
        {
          slotName: 'creditParty',
        },
      ];
    },
    labelActions() {
      if (this.searchList.transactionStatus === 'Pending Authorized') {
        return ['Approve', 'Reject'];
      } else {
        return ['Complete', 'Cancel'];
      }
    },
    statusStyle() {
      return (status) => {
        switch (status) {
          case 'Completed':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case 'Pending':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorSAecondaryWarning}` };
          case 'Full_Refund':
            return { 'background-color': `${this.GlobalStyleVariables.statusErrorColor}` };
          default:
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
        }
      };
    },
    // 处理页面响应式布局
    colXL() {
      return 24 / 4;
    },
    colSmall() {
      return 24 / 3;
    },
    colOptions() {
      const { colXL, colSmall } = this;
      return {
        xs: colSmall * 1,
        sm: colSmall * 1,
        md: colSmall * 1,
        lg: colSmall * 1,
        xl: colXL * 1,
      };
    },
  },
  watch: {
    'searchList.debitPartyType': {
      handler(value) {
        this.searchList.debitParty = '';
        this.searchList.debitPartyId = '';
        if (value === Identity_type.organization && this.searchList.creditPartyType === '5000') {
          this.debitInputTitleLabel = this.$t('TRC.reviewTransaction.label.shortCode');
          this.initCreditParty = 'initCreditParty';
          this.initDebitParty = 'debitParty';
          this.debitRequired = true;
          this.creditRequired = false;
          this.creditSelectOptions = [
            {
              label: 'Organization',
              value: Identity_type.organization,
            },
            {
              label: 'Customer',
              value: Identity_type.customer,
            },
            {
              label: 'Group',
              value: Identity_type.group,
            },
          ];
        } else if (value === Identity_type.customer && this.searchList.creditPartyType === '5000') {
          this.debitInputTitleLabel = this.$t('TRC.reviewTransaction.label.MSISDN');
          this.initCreditParty = 'creditParty';
          this.initDebitParty = 'initDebitParty';
          this.creditRequired = true;
          this.debitRequired = false;

          this.creditSelectOptions = [
            {
              label: 'Organization',
              value: Identity_type.organization,
            },
          ];
        } else if (value === Identity_type.group && this.searchList.creditPartyType === '5000') {
          this.debitInputTitleLabel = this.$t('TRC.reviewTransaction.label.groupCode');
          this.initCreditParty = 'creditParty';
          this.initDebitParty = 'initDebitParty';
          this.creditRequired = true;
          this.debitRequired = false;

          this.creditSelectOptions = [
            {
              label: 'Organization',
              value: Identity_type.organization,
            },
          ];
        } else {
          this.creditRequired = true;
          this.debitRequired = true;
          this.initCreditParty = 'creditParty';
          this.initDebitParty = 'debitParty';
        }
        this.$refs.searchList && this.$refs.searchList.clearValidate();
      },
      immediate: true,
      deep: true,
    },
    'searchList.creditPartyType': {
      handler(value) {
        this.searchList.creditParty = '';
        this.searchList.creditPartyId = '';
        if (value === Identity_type.organization && this.searchList.debitPartyType === '5000') {
          this.creditInputTitleLabel = this.$t('TRC.reviewTransaction.label.shortCode');
          this.initDebitParty = 'debitParty';
          this.debitRequired = true;
          if (this.searchList.debitParty == '' && this.searchList.creditParty == '') {
            this.initCreditParty = 'creditParty';
            this.creditRequired = true;
          }

          this.partySelectOptions = [
            {
              label: 'Organization',
              value: Identity_type.organization,
            },
            {
              label: 'Customer',
              value: Identity_type.customer,
            },
            {
              label: 'Group',
              value: Identity_type.group,
            },
          ];
        } else if (value === Identity_type.customer) {
          this.creditInputTitleLabel = this.$t('TRC.reviewTransaction.label.MSISDN');
          this.debitRequired = true;
          this.creditRequired = false;
          this.initDebitParty = 'debitParty';
          this.initCreditParty = 'initCreditParty';
          this.partySelectOptions = [
            {
              label: 'Organization',
              value: Identity_type.organization,
            },
          ];
        } else if (value === Identity_type.group) {
          this.creditInputTitleLabel = this.$t('TRC.reviewTransaction.label.groupCode');
          this.debitRequired = true;
          this.creditRequired = false;
          this.initDebitParty = 'debitParty';
          this.initCreditParty = 'initCreditParty';
          this.partySelectOptions = [
            {
              label: 'Organization',
              value: Identity_type.organization,
            },
          ];
        } else {
          this.debitRequired = true;
          this.creditRequired = true;
          this.initDebitParty = 'debitParty';
          this.initCreditParty = 'creditParty';
        }
        this.$refs.searchList && this.$refs.searchList.clearValidate();
      },
      immediate: true,
      deep: true,
    },
    'searchList.creditParty': {
      handler() {
        this.$refs.searchList && this.$refs.searchList.clearValidate();
      },
    },
    'searchList.debitParty': {
      handler() {
        this.$refs.searchList && this.$refs.searchList.clearValidate();
      },
    },
  },
  created() {
    this.getReasonTypeList();
  },
  activated() {},
  mounted() {},
  methods: {
    debitReceiverSelected(val) {
      this.debitReceiverData = val || {};
      if (val && val.organizationId) {
        this.initCreditParty = 'initCreditParty';
        this.creditRequired = false;
      } else {
        this.initCreditParty = 'creditParty';
        this.creditRequired = true;
      }
      const objMap = {
        msisdn: 'identityId',
        shortCode: 'organizationId',
        groupCode: 'identityId',
      };
      this.searchList.debitParty = val[this.debitInputField];
    },
    creditReceiverSelected(val) {
      this.creditReceiverData = val || {};
      if (val && val.organizationId) {
        this.initDebitParty = 'initDebitParty';
        this.debitRequired = false;
      } else {
        this.initDebitParty = 'debitParty';
        this.debitRequired = true;
      }
      const objMap = {
        msisdn: 'identityId',
        shortCode: 'organizationId',
        groupCode: 'identityId',
      };
      this.searchList.creditParty = val[this.creditInputField];
    },
    debitCloseCallBack(val) {
      this.debitShortCodeVisible = val;
    },
    creditCoseCallBack(val) {
      this.creditShortCodeVisible = val;
    },

    // 弹窗标题
    handSelect(entityCode) {
      this.oppositePartyType = entityCode;
      this.searchList.debitParty = '';
      this.searchList.debitPartyId = '';
      switch (entityCode) {
        case '1000':
          this.debitDialogTitle = 'Select Customer';
          this.debitInputField = 'msisdn';
          break;
        case '5000':
          this.debitDialogTitle = 'Select Organization'; // Short Code
          this.debitInputField = 'shortCode';
          break;
        case '7000':
          this.debitDialogTitle = 'Select Group Code';
          this.debitInputField = 'groupCode';
          break;
      }
    },
    handSelectChange(entityCode) {
      this.oppositePartyType = entityCode;
      this.searchList.creditParty = '';
      this.searchList.creditPartyId = '';
      switch (entityCode) {
        case '1000':
          this.creditDialogTitle = 'Select Customer';
          this.creditInputField = 'msisdn';
          break;
        case '5000':
          this.creditDialogTitle = 'Select Organization'; // Short Code
          this.creditInputField = 'shortCode';
          break;
        case '7000':
          this.creditDialogTitle = 'Select Group Code';
          this.creditInputField = 'groupCode';
          break;
      }
    },
    getInitTime() {
      let startTime = new Date(dayjs().startOf('date'));
      let endTime = new Date(dayjs().endOf('date'));
      return [startTime, endTime];
    },
    isShowReverse(row) {
      if (!row.reverseMode) {
        return false;
      }
      return (row.transactionStatus === 'Paid' || row.transactionStatus === 'Completed') && row.reverseMode !== 'A';
    },
    isSelectable(row) {
      return row.transactionStatus === 'Authorized' || row.transactionStatus === 'Pending Authorized';
    },
    reset() {
      this.clearSearchListData();
      this.$nextTick(() => {
        this.$refs.searchList && this.$refs.searchList.clearValidate();
      });
    },
    clearSearchListData() {
      this.searchList.initiationTime = this.getInitTime();
      this.searchList.transactionStatus = this.statusOptions[0].value;
      this.searchList.oppositeParty = '';
      this.searchList.reasonType = 'all';
      this.searchList.debitPartyType = Identity_type.organization;
      this.searchList.creditPartyType = Identity_type.organization;
      this.searchList.debitParty = '';
      this.searchList.creditParty = '';
      this.searchList.debitPartyId = '';
      this.searchList.creditPartyId = '';
      this.tradePartyId = '';
      this.searchList.oppositePartyId = '';
      this.searchList.oppositePartyType = '';
      this.initDebitParty = 'debitParty';
      this.initCreditParty = 'creditParty';
      this.debitRequired = true;
      this.creditRequired = true;
      this.debitDialogTitle = 'Select Organization';
      this.creditDialogTitle = 'Select Organization';
      this.debitInputField = 'shortCode';
      this.creditInputField = 'shortCode';
    },
    search() {
      this.searchLoading = true;
      this.$refs['searchList'].innerFormRef.validate((valid) => {
        if (!valid) {
          this.searchLoading = false;
          return;
        }
        this.paginationConfig = {
          total: 0,
          pageIndex: 1,
          pageSize: 10,
        };
        this.refreshTableFlag = true;
      });
    },
    showDetail(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.tradePartyId,
        identityType: this.entityIdentityType,
        status: row.transactionStatus,
        type: 'detail',
        from: 'transaction',
      };
      this.openTransactionDetail(params);
    },
    reverse(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.tradePartyId,
        identityType: this.entityIdentityType,
        type: 'reverse',
        from: 'transaction',
      };
      this.openTransactionDetail(params);
    },

    getTableData() {
      this.searchLoading = true;
      return new Promise((resolve) => {
        this.getIdentityId();
        this.getTradePartyId();
        let params = {
          identityType: this.entityIdentityType,
          tradePartyId: this.tradePartyId,
          createTimeBegin: this.searchList.initiationTime
            ? dayjs(this.searchList.initiationTime[0]).format('YYYY-MM-DD HH:mm:ss')
            : '',
          createTimeEnd: this.searchList.initiationTime
            ? dayjs(this.searchList.initiationTime[1]).format('YYYY-MM-DD HH:mm:ss')
            : '',
          accountNo: '',
          oppositePartyId: this.searchList.oppositePartyId,
          oppositePartyType: this.searchList.oppositePartyType,
          transactionRole: this.transactionRole,
          reasonType: this.searchList.reasonType === 'all' ? '' : this.searchList.reasonType,
          status: this.searchList.transactionStatus === 'all' ? [] : [this.searchList.transactionStatus],
          startNumber: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
        };
        queryTransactionList(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = res.totalSize || 0;
              res.list = res.spOrderQueryDTOList || [];
              res.list = res.list.map((order) => {
                if (order.transactionAmount) {
                  order.transactionAmount = AmountUnit.format(order.transactionAmount, order.transactionCurrency);
                }
                order.createTime = dayjs(order.createTime).format('DD/MM/YYYY HH:mm:ss');
                order.expireTime = dayjs(order.expireTime).format('DD/MM/YYYY HH:mm:ss');
                this.refreshTableFlag = false;
                this.searchLoading = false;
                return order;
              });
            } else {
              res.totalRecords = 0;
              res.list = [];
            }
            resolve(res);
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },

    async getReasonTypeList() {
      let res = await queryReasonType();
      if (res.header && res.header.success) {
        res.reasonTypeList.map((item) => {
          let reasonTypeObj = {
            value: item.reasonIndex,
            label: item.reasonName,
          };
          this.reasonTypeOptions.push(reasonTypeObj);
        });
      }
    },

    async searchTransaction() {
      this.receiptNoExist = true;
      this.$refs['receiptNoFormRef'].validate(async (valid) => {
        if (valid) {
          this.searchReceiptNoLoading = true;
          let res = await searchTransactionByOrderId({ tradeOrderId: this.receiptNoForm.receiptNo });
          this.searchReceiptNoLoading = false;
          if (res.header && res.header.success) {
            if (res.identityId) {
              let params = {
                tradeOrderId: this.receiptNoForm.receiptNo,
                identityId: res.identityId,
                identityType: res.identityType,
                from: 'transaction',
              };
              this.receiptNoExist = true;
              this.openTransactionDetail(params);
            } else {
              // receiptNo不存在，重新校验
              this.receiptNoExist = false;
            }
            this.$refs['receiptNoFormRef'].validateField('receiptNo');
          }
        }
      });
    },

    orgShortCodeBlur(shortCode) {
      return new Promise((resolve) => {
        let params = {
          shortCode: shortCode || '',
        };
        getOrganizationList(params).then((res) => {
          this.searchLoading = false;
          if (res.header && res.header.success && res.organizationInfo) {
            resolve(res.organizationInfo.organizationId);
          } else {
            resolve('');
          }
        });
      });
    },
    getTradePartyId() {
      if (!this.searchList.debitParty && this.searchList.creditParty) {
        this.tradePartyId = this.creditReceiverData.organizationId;
        this.oppositePartyId = '';
        this.oppositePartyType = '';
      } else if (this.searchList.debitParty && !this.searchList.creditParty) {
        this.tradePartyId = this.debitReceiverData.organizationId;
        this.searchList.oppositePartyId = '';
        this.searchList.oppositePartyType = '';
      } else {
        if (this.debitReceiverData.organizationId) {
          this.tradePartyId = this.debitReceiverData.organizationId;
          this.searchList.oppositePartyId =
            this.creditReceiverData.organizationId || this.creditReceiverData.identityId;
          this.searchList.oppositePartyType = this.searchList.creditPartyType;
        } else if (this.debitReceiverData.identityId) {
          this.tradePartyId = this.debitReceiverData.identityId;
          this.searchList.oppositePartyId = this.creditReceiverData.organizationId;
          this.searchList.oppositePartyType = this.searchList.creditPartyType;
        }
      }
    },
    getIdentityId() {
      if (!this.searchList.debitParty && this.searchList.creditParty) {
        this.transactionRole = '0';
        this.entityIdentityType = this.searchList.creditPartyType;
      } else if (this.searchList.debitParty && !this.searchList.creditParty) {
        this.transactionRole = '1';
        this.entityIdentityType = this.searchList.debitPartyType;
      } else {
        this.transactionRole = '1';
        this.entityIdentityType = this.searchList.creditPartyType;
      }
    },
    verifyTransactionCode(value) {
      if (!value.length) {
        return;
      }
      return new Promise((resolve) => {
        let params = { shortCode: value };
        if (this.entityIdentityType === Identity_type.customer) {
          params = { msisdn: value };
        } else {
        }
        verifyTransactionIdentity(params).then((res) => {
          if (res.header && res.header.success && res.identityId) {
            resolve(res.identityId);
          } else {
            resolve('');
          }
        });
      });
    },

    openDebitShortCodeDialog() {
      this.debitShortCodeVisible = true;
    },
    openCreditShortCodeDialog() {
      this.creditShortCodeVisible = true;
    },
    handleProcess() {
      if (!this.checkedList.length) {
        this.$modal.msgError('Please select at least one.');
        return;
      }
      this.showProcessDialog = true;
    },
    goToProcessPage(row) {
      let params = {
        tradeOrderId: row.tradeOrderId,
        identityId: this.tradePartyId,
        identityType: this.entityIdentityType,
        type: 'process',
        from: 'transaction',
      };
      this.openTransactionDetail(params);
    },
    openTransactionDetail(params) {
      const path = '/mainPage/identifierCenter/organizationInfo/organization/transactionDetail';
      this.openNewWindow({ path: path, query: { params: encodeURIComponent(JSON.stringify(params)) } });
    },
    selectTableDataChanged(val) {
      this.checkedList = val;
    },
    searchByReceiptNo() {
      this.searchTransaction();
    },
    submitProcessDialog() {
      this.$refs['processingForm'].validate(async (valid) => {
        if (valid) {
          let action = this.processingForm.action;
          let tradeOrderIdList = [];
          this.checkedList.map((item) => {
            tradeOrderIdList.push(item.tradeOrderId);
          });
          let params = {};
          let res;
          if (action === 'approve' || action === 'reject') {
            params = {
              actionType: action,
              remark: this.processingForm.remark,
              tradeOrderIdList,
            };
            res = await batchApprovalTransaction(params);
          } else {
            params = {
              operationType: action === 'cancel' ? 'CANCEL' : 'CONFIRM',
              tradeOrderIdList,
              remark: this.processingForm.remark,
              serviceCode: '',
              reasonType: '',
            };
            res = await confirmTransaction(params);
          }
          if (res.header && res.header.success) {
            this.processingForm = { remark: '', action: '' };
            this.showProcessDialog = false;
            this.checkedList = [];
            this.refreshTableFlag = true;
          }
        }
      });
    },
    closeProcessDialog() {
      this.showProcessDialog = false;
    },
  },
};
</script>

